header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: rgb(240, 240, 240);
  box-shadow: 0 0 5px 5px rgb(0 0 0 / 10%);
  z-index: 1;
}

.logo {
  width: calc(100% - 70px);
  text-align: center;
}

.logo img {
  width: 15%;
  cursor: pointer;
}

.header-container {
  display: flex;
  justify-content: space-between;
}
.menu {
  width: 70px;
  display: inline-flex;
  align-items: center;
}

.menu svg {
  font-size: 40px;
}

.menu button {
  outline: none;
  border: none;
  cursor: pointer;
  position: fixed;
  left: 10px;
  animation: none;
  padding: 3px 5px 0px 5px;
}

.menu button.visible {
  animation: toggle 0.8s ease-in-out forwards;
}

@keyframes toggle {
  0% {
    left: 0;
  }
  100% {
    left: 260px;
  }
}

.news-flex {
  display: flex;
  border: 1px solid #0000001f;
  padding: 10px;
  gap: 10px;
  border-radius: 10px;
  box-shadow: 0 0 10px 1px rgb(0 0 0 / 8%);
}

.news {
  margin: auto;
  /* max-width: 50%; */
}

.news:hover {
  transform: scale(1.01);
  transition: all 0.3s ease-in-out;
}

.newsImage {
  width: 40%;
  border-radius: 10px;
  overflow: hidden;
  max-height: 150px;
}

.newsImage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.categoryName {
  /* margin: 90px 0 15px 0; */
  display: flex;
  justify-content: center;
}
.news-container {
  display: grid;
  justify-content: center;
  gap: 20px;
  margin: 90px 0 15px 0;
}

@keyframes slideIn {
  0% {
    left: -250px; /* Start off-screen */
  }
  100% {
    left: 0; /* End at the visible position */
  }
}

.nav-container {
  position: fixed;
  top: 0;
  left: -250px; /* Start off-screen */
  width: 250px; /* Set to the desired width */
  height: 100%;
  background-color: black;
  animation: none;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.2); /* Optional shadow for depth */
  z-index: 1000;
}

.nav-container.visible {
  animation: slideIn 0.8s ease-in-out forwards;
}

.close {
  transform: translateX(260px);
}

.col2 {
  width: 45%;
  display: inline-grid;
  gap: 20px;
}

.column1 {
  padding: 2px 10px;
}

.column2 {
  border: 1px solid #00000021;
  border-radius: 10px;
  /* padding: 15px 25px; */
  overflow: hidden;
}

.scrollbar {
  height: 100vh;
  overflow-y: scroll;
}

.scrollbar::-webkit-scrollbar {
  width: 8px;
  height: 10px;
}

.scrollbar::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

.menu-list {
  display: grid;
  justify-content: normal;
  /* margin: 10px 0; */
}

.menu-list button {
  outline: 0;
  border: 0;
  background-color: transparent;
  padding: 15px 20px;
  color: white;
  text-align: left;
  border-bottom: 1px solid #ffffff2b;
  cursor: pointer;
  text-transform: capitalize;
}

.nav h3 {
  font-size: 30px;
  text-align: center;
  border-bottom: 2px solid #ffffff4f;
  padding: 14px;
  color: white;
}

.news-info {
  position: relative;
}

iframe {
  border: 0;
  width: 100%;
}

.news-info .title {
  display: -webkit-box;
  -webkit-line-clamp: 1; 
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 24px;
  text-transform: capitalize;
}

small {
  font-size: 12px;
  color: #808080cf;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.description {
  font-size: 14px;
  margin: 10px 0;
  color: #333;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  min-height: 60px;
}

.read-more {
  font-size: 12px;
  color: #350a0e;
  font-weight: 600;
  transition: all 0.3s ease-in-out;
  text-decoration: none;
}

.read-more:hover {
  color: #ed7680;
}

.news-info {
  width: 70%;
  text-align: left;
}
.newsLink {
  text-decoration: none;
  color: unset;
  outline: 0;
  border: 0;
  background: transparent;
  text-align: left;
  cursor: pointer;
}

.nav .nav-logo {
  text-align: center;
  padding: 3px;
  background: #f0f0f0;
}

.nav-logo img {
  width: 73%;
}

.menu-list p {
  font-size: 14px;
  color: #f0f0f0d6;
  padding: 12px 0 8px 5px;
}

.inner-container {
  display: flex;
  justify-content: center;
}
.close-button {
  text-align: right;
}
.close-button svg {
  margin: 10px 10px 0 0;
  font-size: 20px;
  cursor: pointer;
}

.footer {
  padding: 20px;
  text-align: center;
  border-top: 1px solid #00000017;
}
.loadmore-container {
  display: flex;
  justify-content: center;
  padding: 5px 0;
}

.loadmore-container button {
  border: 1px solid #6a6a6a12;
  outline: 0;
  padding: 10px 20px;
  border-radius: 5px;
  box-shadow: 0 0 15px 2px rgb(0 0 0 / 10%);
  cursor: pointer;
  transition: all 0.4s ease-in-out;
}

.loadmore-container button:hover {
  background-color: #3c3c3c;
  color: white;
}

.loader-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  max-height: 75vh;
  padding: 20%;
}

.loader-container img {
  width: 15%;
}

iframe {
  height: 100vh;
}

.news.active {
  background-color: #dfdfdf;
  border-radius: 10px;
  box-shadow: 0 0 11px 1px #32060a6b;
}

@media screen and (max-width: 450px) {
  .news {
    max-width: 100%;
    /* margin: 0 15px; */
  }

  .col2 {
    width: 100%;
  }
  .news-info .title {
    font-size: 18px;
  }
  .description {
    font-size: 13px;
    min-height: 55px;
  }
  .logo img {
    width: 50%;
  }
  .menu svg {
    font-size: 32px;
  }
  .news-container {
    margin: 75px 0 15px 0;
  }
  .column2 {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 60%;
    width: 90%;
    background-color: #ffffff;
    box-shadow: 0 0 20px 2px #aba8a8;
    z-index: 5;
    overflow: auto;
  }

  .close-button {
    text-align: right;
  }
  .close-button svg {
    margin: 10px 10px 0 0;
    font-size: 20px;
  }
  .shadow {
    position: absolute;
    background: #f0f8ff96;
    height: 100%;
    width: 100%;
    top: 0;
    z-index: 4;
  }
  iframe {
    height: 54vh;
  }
  .no-scroll {
    overflow: hidden;
    height: 100%;
  }
}

@media screen and (min-width: 768px) and (max-width: 900px) {
  .news-container {
    margin: 100px 0 15px 0;
  }
  .col2 {
    width: 80%;
  }
  .column2 {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 60%;
    width: 90%;
    background-color: #ffffff;
    box-shadow: 0 0 20px 2px #aba8a8;
    z-index: 5;
    overflow: auto;
  }

  iframe {
    height: 54vh;
  }
  .close-button {
    text-align: right;
  }
  .close-button svg {
    margin: 10px 10px 0 0;
    font-size: 20px;
  }
  .shadow {
    position: absolute;
    background: #f0f8ff96;
    height: 100%;
    width: 100%;
    top: 0;
    z-index: 4;
  }
  .logo img {
    width: 30%;
  }

  .no-scroll {
    overflow: hidden;
    height: 100%;
  }
}
